function getTotalDamage(state) {
	let {attacker, defender, ...others} = state
	if ([attacker.weapon, defender.weapon].includes("")) {
		return 0
	}
	let [attackerStat, defenderStat] = decideStats(attacker, defender)
	let advantage = getColorAdvantage(attacker, defender)
	let effectiveness = getEffectiveness(others.effective)
	let totalDamage = Math.floor(attackerStat * effectiveness) + Math.floor(Math.floor(attackerStat * effectiveness) * advantage) - defenderStat
	return positive(totalDamage)
}

function decideStats(attacker, defender) {
	if (["bow", "axe", "lance", "sword", "dagger"].includes(attacker.weapon)) return [attacker.stats.atk, defender.stats.def]
	return [attacker.stats.atk, defender.stats.res]
}

function getEffectiveness(effective) {
	if (effective) return 1.5
	return 1
}

function getColorAdvantage(attacker, defender) {
	let effectivenessTriangle = {
		"red": "green",
		"green": "blue",
		"blue": "red"
	}
	if (effectivenessTriangle[attacker.color] === defender.color) return 0.2
	if (effectivenessTriangle[defender.color] === attacker.color) return -0.2
	return 0
}

function positive(number) {
	return Math.max(0, number)
}

export default getTotalDamage
