import React, {Component} from "react"

class Side extends Component {
	constructor() {
		super()
	}

	createStatInput(stat) {
		return <input type="number" onChange={this.props.updateStat} maxLength="2" name={stat} className="stat" min={1} value={this.props.data.stats[stat]} />
	}

	render() {
		return (
			<div className="side" id={this.props.side}>
				<h3 style={{color: this.props.side === "attacker" ? "blue" : "red"}}>{format(this.props.side)}</h3>
				<div className="stats">
					<table>
						<tbody>
							<tr><td style={{textAlign: "center", verticalAlign: "middle", colspan: 2}}>HP {this.createStatInput("hp")}</td></tr>
							<tr><td>Atk {this.createStatInput("atk")}</td><td>Spd {this.createStatInput("spd")}</td></tr>
							<tr><td>Def {this.createStatInput("def")}</td><td>Res {this.createStatInput("res")}</td></tr>
						</tbody>
					</table>
				</div>
				<div className="weapon">
					<div className="essentials">
						<table>
							<tbody>
								<tr>
									<td>
										Weapon Type <select id="weaponType" onChange={this.props.updateWeapon}>
											<option value=""></option>
											<option value="sword">Sword</option>
											<option value="lance">Lance</option>
											<option value="axe">Axe</option>
											<option value="tome">Tome</option>
											<option value="dragonstone">Dragonstone</option>
											<option value="bow">Bow</option>
											<option value="dagger">Dagger</option>
											<option value="beast">Laguz / Beast</option>
										</select>
									</td>
									<td>
										Color <select id="weaponColor" disabled={this.props.data.lockColor} onChange={this.props.updateColor}>
											<option value="colorless">Colorless</option>
											<option value="red">Red</option>
											<option value="blue">Blue</option>
											<option value="green">Green</option>
										</select>
									</td>
								</tr>
							</tbody>
						</table>
					</div>
					<div className="movement">
						Movement Type <select id="movement" onChange={this.props.updateMovement}>
									<option value="infantry">Infantry</option>
									<option value="flyer">Flyer</option>
									<option value="cavalry">Cavalry</option>
									<option value="armored">Armored</option>
								</select>
					</div>
					<div className="effectiveness">
						Effective against opponent's movement <input type="checkbox" name="movement" disabled={this.props.side === "defender"} onChange={this.props.updateEffectiveness} /> <br />
						Effective against opponent's weapon <input type="checkbox" name="weapon" disabled={this.props.side === "defender"} onChange={this.props.updateEffectiveness} />
					</div>
				</div>
			</div>
		)
	}
}

function format(word) {
  return word[0].toUpperCase() + word.substring(1, word.length)
}

export default Side
