import React, { Component } from 'react';
import './App.css';
import Side from "./side.jsx"
import calculateDamage from "./calc.js"
import ResultBox from "./resultbox.jsx"

class App extends Component {
  constructor() {
    super()
    this.state = {
      attacker: {
        movement: "",
        weapon: "",
        color: "",
        stats: {
          hp: 1,
          atk: 1,
          spd: 1,
          def: 1,
          res: 1
        },
        lockColor: false
      },
      produceResult: false,
      damage: 0,
      percentage: 0,
      defender: {
        movement: "",
        weapon: "",
        color: "",
        stats: {
          hp: 1,
          atk: 1,
          spd: 1,
          def: 1,
          res: 1
        },
        lockColor: false
      },
      effectiveAgainstMovement: false,
      effectiveAgainstWeapon: false,
      effective: false
    }
  }

  createNewSide = side => {
    return <Side side={side}
      updateEffectiveness={this.updateEffectiveness(side)}
      updateColor={this.updateColor(side)}
      updateWeapon={this.updateWeapon(side)}
      updateMovement={this.updateMovement(side)}
      updateStat={this.updateStat(side)}
      data={this.state[side]}
      />
  }

  calculateDamage = () => {
    console.log(calculateDamage(this.state))
    let damage = calculateDamage(this.state) || 0
    let percentage = Math.min(Math.trunc(damage / this.state.defender.stats.hp * 100), 100)
    this.setState({
      produceResult: Boolean(damage),
      damage,
      percentage
    })
  }

  updateColor = side => {
    return e => {
      let color = e.target.value
      let currentSide = {...this.state[side]}
      currentSide.color = color
      this.setState({
        [side]: currentSide,
      }, () => {
        this.calculateDamage()
      })
    }
  }

  updateEffectiveness = side => {
    if (side === "attacker") {
      return e => {
        let whichEffectiveness = format(e.target.name)
        let effectivenessTemplate = `effectiveAgainst${whichEffectiveness}`
        let otherEffectiveness = "weapon" !== whichEffectiveness ? "weapon" : "movement"
        let currentState = this.state[effectivenessTemplate]
        this.setState({
          [effectivenessTemplate]: !currentState,
          effective: !currentState || this.state[`effectiveAgainst${format(otherEffectiveness)}`]
        }, () => {
          this.calculateDamage()
        })
      }
    }
    return () => {}
  }

  updateMovement = side => {
    return e => {
      let value = e.target.value
      let sidee = {...this.state[side]}
      sidee.movement = value
      this.setState({
        [side]: sidee
      }, () => {
        this.calculateDamage()
      })
    }
  }

  updateStat = side => { // arrow functions are used to keep the original reference of "this" as the App class
    return e => {
      let value = e.target.value
      let sideStats = {...this.state[side]}
      let stat = e.target.name
      sideStats.stats[stat] = +value
      this.setState({
        [side]: sideStats
      }, () => {
        this.calculateDamage()
      })
    }
  }

  updateWeapon = side => {
    return e => {
      let value = e.target.value
      let sidee = {...this.state[side]}
      sidee.weapon = value
      let lockColor = ["axe", "sword", "lance"].includes(sidee.weapon)
      if (lockColor) {
        let forcedColor = {
          "axe": "green",
          "sword": "red",
          "lance": "blue"
        }[sidee.weapon]
        sidee.color = forcedColor
      }

      sidee.lockColor = lockColor

      this.setState({
        [side]: sidee
      }, () => {
        this.calculateDamage()
      })
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="App">
          <h2>Fire Emblem Heroes Simple Damage Calculator</h2>
          <div className="attacker">
            {this.createNewSide("attacker")}
          </div>
          <ResultBox>
            {this.state.produceResult ? `The attacker's settings yield a damage amount of ${this.state.damage} HP lost (${this.state.percentage}%).` : "The attacker's settings do not yield any damage on the defender."}
          </ResultBox>
          <div className="defender">
            {this.createNewSide("defender")}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

function format(word) {
  return word[0].toUpperCase() + word.substring(1, word.length)
}

export default App;
